import React, { useState } from "react";
import "./Calendar.css";
import { useTracker } from "../../context/TrackerContext";
import { useNavigate } from "react-router-dom";
import Header from "../../components/Header";
import MonthSelector from "../tracker/MonthSelector";

const Calendar = () => {
  const { state, dispatch } = useTracker();
  const { subjects, checkboxes, dayNotes, currentMonth } = state;
  const navigate = useNavigate();
  const [selectedDate, setSelectedDate] = useState<string | null>(null);
  const [selectedSubject, setSelectedSubject] = useState<string | null>(null);
  const [popupNotes, setPopupNotes] = useState("");
  const [generalNotes, setGeneralNotes] = useState<string>((dayNotes[currentMonth]?.["general"] as unknown) as string || "");
  const [viewNotes, setViewNotes] = useState<{ date: string; notes: string } | null>(null);

  const handleDayClick = (day: number) => {
    const [year, month] = currentMonth.split("-").map(Number); // month is 0-based from split
    const date = `${month + 1}/${day}/${year}`; // Adjust month to 1-based for display
    setSelectedDate(date);
    setSelectedSubject(null);
    setPopupNotes("");
    const notesKey = Object.keys(dayNotes[currentMonth] || {}).find((key) => key.startsWith(`${month + 1}/${day}/${year}`));
    if (notesKey) {
      setViewNotes({ date, notes: (dayNotes[currentMonth][notesKey] as unknown) as string || "" });
    } else {
      setViewNotes(null);
    }
  };

  const handleTrackProgress = () => {
    if (selectedDate && selectedSubject) {
      const [, day] = selectedDate.split("/").map(Number);
      const dayIndex = day - 1;
      const subjectIndex = subjects.indexOf(selectedSubject);

      if (subjectIndex >= 0) {
        const newCheckboxes = checkboxes[currentMonth].map((row: boolean[], i: number) =>
          i === subjectIndex ? row.map((checked: boolean, j: number) => (j === dayIndex ? !checked : checked)) : row
        );
        dispatch({ type: "SET_CHECKBOXES", payload: { month: currentMonth, value: newCheckboxes } });
        if (popupNotes.trim()) {
          const key = `${subjectIndex}_${dayIndex}`;
          dispatch({ type: "SET_DAY_NOTES", payload: { month: currentMonth, date: selectedDate, key, notes: popupNotes } });
        }
        setSelectedDate(null);
        setSelectedSubject(null);
      }
    }
  };

  const handleGeneralNotesChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    const value = e.target.value;
    setGeneralNotes(value);
    dispatch({
      type: "SET_DAY_NOTES",
      payload: { month: currentMonth, date: "general", key: "general", notes: value },
    });
  };

  const hasNotes = (day: number) => {
    const [year, month] = currentMonth.split("-").map(Number);
    const date = `${month + 1}/${day}/${year}`; // Use 1-based month for consistency
    return Object.keys(dayNotes[currentMonth] || {}).some((key) => key.startsWith(`${date}`));
  };

  const daysInMonth = new Date(new Date(currentMonth).getFullYear(), new Date(currentMonth).getMonth() + 1, 0).getDate();
  const firstDay = new Date(new Date(currentMonth).getFullYear(), new Date(currentMonth).getMonth(), 1).getDay();
  const [year, month] = currentMonth.split("-").map(Number); // Move outside loop

  const calendarRows = [];
  for (let i = 0; i < Math.ceil((firstDay + daysInMonth) / 7); i++) {
    calendarRows.push(
      <tr key={i}>
        {Array.from({ length: 7 }, (_, j) => {
          const dayIndex = i * 7 + j - firstDay + 1;
          return dayIndex > 0 && dayIndex <= daysInMonth ? (
            <td
              key={j}
              className={`border border-gray-light p-2 text-center cursor-pointer ${
                selectedDate === `${month + 1}/${dayIndex}/${year}` ? "bg-lavender" : ""
              }`}
              onClick={() => handleDayClick(dayIndex)}
            >
              {dayIndex}
              {hasNotes(dayIndex) && <span className="text-blue-500 ml-1">📝</span>}
            </td>
          ) : (
            <td key={j} className="border border-gray-light p-2"></td>
          );
        })}
      </tr>
    );
  }

  const monthName = new Date(currentMonth).toLocaleString("default", { month: "long", year: "numeric" });

  return (
    <div className="p-4">
      <Header currentMonth={currentMonth} />
      <h1 className="text-2xl sm:text-3xl font-bold mb-4 font-sacramento text-lavender-dark text-center">
        {monthName} Calendar
      </h1>
      <div className="w-full mb-4 flex flex-col sm:flex-row gap-2">
        <MonthSelector currentMonth={currentMonth} onSelect={(month: string) => dispatch({ type: "SET_CURRENT_MONTH", payload: month })} />
        <button
          onClick={() => navigate("/")}
          className="px-4 py-2 bg-blue-light text-gray-800 rounded-full hover:bg-blue-200 mb-4 text-sm sm:text-base shadow-soft touch-target push-button"
        >
          Back to Tracker
        </button>
      </div>
      <table className="w-full border-collapse mx-auto calendar-large">
        <thead>
          <tr className="bg-lavender">
            <th className="border border-gray-light p-4">Sun</th>
            <th className="border border-gray-light p-4">Mon</th>
            <th className="border border-gray-light p-4">Tue</th>
            <th className="border border-gray-light p-4">Wed</th>
            <th className="border border-gray-light p-4">Thu</th>
            <th className="border border-gray-light p-4">Fri</th>
            <th className="border border-gray-light p-4">Sat</th>
          </tr>
        </thead>
        <tbody>{calendarRows}</tbody>
      </table>
      <div className="mt-4">
        <h2 className="text-lg sm:text-xl font-bold mb-2 font-sacramento text-lavender-dark">General Notes</h2>
        <textarea
          value={generalNotes}
          onChange={handleGeneralNotesChange}
          className="w-full p-2 border border-gray-light rounded-lg"
          style={{ minHeight: "100px" }}
          placeholder="Enter general notes for this month..."
        />
      </div>
      {selectedDate && (
        <div className="fixed inset-0 bg-black bg-opacity-70 flex items-center justify-center z-50">
          <div className="popup bg-white p-4 rounded-lg shadow-lg w-11/12 sm:w-2/3 md:w-1/2">
            <div className="popup-header">
              <h3 className="text-lg font-sacramento text-lavender-dark mb-2">Day: {selectedDate}</h3>
              {viewNotes && (
                <div className="mb-2">
                  <h4 className="text-md font-bold">Existing Notes:</h4>
                  <p>{viewNotes.notes}</p>
                </div>
              )}
            </div>
            <div className="popup-content flex flex-col gap-2">
              <h4 className="text-md font-bold mb-1">Subjects</h4>
              <select
                value={selectedSubject || ""}
                onChange={(e) => setSelectedSubject(e.target.value)}
                className="p-1 border border-gray-light rounded-lg text-sm bg-white"
                style={{ height: "2em" }}
              >
                <option value="">Select a Subject</option>
                {subjects.map((subject: string, index: number) => (
                  <option key={index} value={subject}>
                    {subject}
                  </option>
                ))}
              </select>
              <textarea
                className="popup textarea p-2 border border-gray-light rounded-lg"
                placeholder="Add notes for this day..."
                value={popupNotes}
                onChange={(e) => setPopupNotes(e.target.value)}
                style={{ minHeight: "100px" }}
              />
            </div>
            <div className="popup-buttons mt-2 flex gap-2">
              {selectedSubject && (
                <button
                  onClick={handleTrackProgress}
                  className="bg-mint text-gray-800 hover:bg-mint-dark p-2 rounded-full"
                >
                  Track Progress
                </button>
              )}
              <button
                onClick={() => {
                  setSelectedDate(null);
                  setSelectedSubject(null);
                  setViewNotes(null);
                }}
                className="bg-pink-light text-gray-800 hover:bg-pink-200 p-2 rounded-full"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Calendar;