import React from "react";

interface HeaderProps {
  headerFont?: string; // Make optional with a default
  currentMonth?: string; // Add currentMonth prop
}

const Header: React.FC<HeaderProps> = ({ headerFont = "Sacramento", currentMonth }) => {
  const headerStyle = {
    fontFamily: `${headerFont}, cursive`,
    color: "#4B0082", // Matches text-lavender-dark
    textAlign: "center" as const,
  };

  const displayMonth = currentMonth
    ? new Date(currentMonth).toLocaleString("default", { month: "long", year: "numeric" })
    : null;

  return (
    <header style={headerStyle}>
      <h1 className="font-sacramento text-lavender-dark text-2xl sm:text-3xl">Nutshell</h1>
      {displayMonth && <h2 className="font-sacramento text-lavender-dark">{displayMonth}</h2>}
    </header>
  );
};

export default Header;