import React, { createContext, useReducer, ReactNode, useContext } from "react";

interface TrackerState {
  studentName: string;
  subjects: string[];
  checkboxes: Record<string, boolean[][]>; // month -> checkboxes
  lessonSubject1: string;
  lessonSubject2: string;
  lessonSubject3: string;
  lessonSubject4: string;
  lessonGrid1: string[];
  lessonGrid2: string[];
  lessonGrid3: string[];
  lessonGrid4: string[];
  headerFont: string;
  fixedFont: string;
  dayFont: string;
  textBoxFont: string;
  textBoxFontSize: number;
  textBoxAlignment: "left" | "center" | "right";
  selectedSubject: string | null;
  showSettings: boolean;
  showResetConfirm: boolean;
  completedLessons: Record<string, Record<string, string[][]>>; // month -> subject -> lessons
  dayNotes: Record<string, Record<string, Record<string, string>>>; // month -> date -> subjectIndex_dayIndex -> notes
  currentMonth: string; // e.g., "2025-06"
}

type TrackerAction =
  | { type: "SET_STUDENT_NAME"; payload: string }
  | { type: "SET_SUBJECTS"; payload: string[] }
  | { type: "SET_CHECKBOXES"; payload: { month: string; value: boolean[][] } }
  | { type: "SET_LESSON_SUBJECT"; payload: { index: number; value: string } }
  | { type: "SET_LESSON_GRID"; payload: { index: number; value: string[] } }
  | { type: "SET_FONT"; payload: { type: "header" | "fixed" | "day" | "textBox"; value: string } }
  | { type: "SET_TEXTBOX_STYLE"; payload: { type: "size" | "alignment"; value: number | string } }
  | { type: "SET_SELECTED_SUBJECT"; payload: string | null }
  | { type: "TOGGLE_SETTINGS" }
  | { type: "TOGGLE_RESET_CONFIRM" }
  | { type: "RESET_ALL" }
  | { type: "SET_DAY_NOTES"; payload: { month: string; date: string; key: string; notes: string } }
  | { type: "SET_COMPLETED_LESSONS"; payload: { month: string; value: Record<string, string[][]> } }
  | { type: "SET_CURRENT_MONTH"; payload: string };

const getInitialCheckboxes = (subjectsLength: number) =>
  Array(subjectsLength).fill(null).map(() => Array(31).fill(false));

const initialState: TrackerState = {
  studentName: "",
  subjects: ["Subject 1", "Subject 2"],
  checkboxes: {
    "2025-06": getInitialCheckboxes(2), // Default to current month
  },
  lessonSubject1: "",
  lessonSubject2: "",
  lessonSubject3: "",
  lessonSubject4: "",
  lessonGrid1: Array(16).fill(""),
  lessonGrid2: Array(16).fill(""),
  lessonGrid3: Array(16).fill(""),
  lessonGrid4: Array(16).fill(""),
  headerFont: "Sacramento",
  fixedFont: "Sacramento",
  dayFont: "Sacramento",
  textBoxFont: "Sacramento",
  textBoxFontSize: 16,
  textBoxAlignment: "left",
  selectedSubject: null,
  showSettings: false,
  showResetConfirm: false,
  completedLessons: {
    "2025-06": {},
  },
  dayNotes: {
    "2025-06": JSON.parse(localStorage.getItem("dayNotes_2025-06") || "{}"),
  },
  currentMonth: "2025-06", // Default to current month
};

const trackerReducer = (state: TrackerState, action: TrackerAction): TrackerState => {
  switch (action.type) {
    case "SET_STUDENT_NAME":
      return { ...state, studentName: action.payload };
    case "SET_SUBJECTS":
      const newCheckboxes = { ...state.checkboxes };
      const subjectsLength = action.payload.length;
      Object.keys(newCheckboxes).forEach((month) => {
        newCheckboxes[month] = getInitialCheckboxes(subjectsLength);
      });
      return { ...state, subjects: action.payload, checkboxes: newCheckboxes };
    case "SET_CHECKBOXES":
      return {
        ...state,
        checkboxes: { ...state.checkboxes, [action.payload.month]: action.payload.value },
      };
    case "SET_LESSON_SUBJECT":
      return {
        ...state,
        [`lessonSubject${action.payload.index}`]: action.payload.value,
      };
    case "SET_LESSON_GRID":
      return {
        ...state,
        [`lessonGrid${action.payload.index}`]: action.payload.value,
      };
    case "SET_FONT":
      return {
        ...state,
        [action.payload.type + "Font"]: action.payload.value,
      };
    case "SET_TEXTBOX_STYLE":
      return {
        ...state,
        textBoxFontSize: action.payload.type === "size" ? (action.payload.value as number) : state.textBoxFontSize,
        textBoxAlignment:
          action.payload.type === "alignment" ? (action.payload.value as "left" | "center" | "right") : state.textBoxAlignment,
      };
    case "SET_SELECTED_SUBJECT":
      return { ...state, selectedSubject: action.payload };
    case "TOGGLE_SETTINGS":
      return { ...state, showSettings: !state.showSettings };
    case "TOGGLE_RESET_CONFIRM":
      return { ...state, showResetConfirm: !state.showResetConfirm };
    case "RESET_ALL":
      const newInitialCheckboxes = getInitialCheckboxes(state.subjects.length);
      return {
        ...state,
        checkboxes: { [state.currentMonth]: newInitialCheckboxes },
        completedLessons: { [state.currentMonth]: {} },
        dayNotes: { [state.currentMonth]: {} },
      };
    case "SET_DAY_NOTES":
      const { month, date, key, notes } = action.payload;
      const updatedNotes = {
        ...state.dayNotes,
        [month]: { ...(state.dayNotes[month] || {}), [date]: { ...(state.dayNotes[month]?.[date] || {}), [key]: notes } },
      };
      localStorage.setItem(`dayNotes_${month}`, JSON.stringify(updatedNotes[month]));
      return { ...state, dayNotes: updatedNotes };
    case "SET_COMPLETED_LESSONS":
      return {
        ...state,
        completedLessons: { ...state.completedLessons, [action.payload.month]: action.payload.value },
      };
    case "SET_CURRENT_MONTH":
      if (!state.checkboxes[action.payload]) {
        state.checkboxes[action.payload] = getInitialCheckboxes(state.subjects.length);
      }
      if (!state.completedLessons[action.payload]) {
        state.completedLessons[action.payload] = {};
      }
      if (!state.dayNotes[action.payload]) {
        state.dayNotes[action.payload] = JSON.parse(localStorage.getItem(`dayNotes_${action.payload}`) || "{}");
      }
      return { ...state, currentMonth: action.payload };
    default:
      return state;
  }
};

const TrackerContext = createContext<{
  state: TrackerState;
  dispatch: React.Dispatch<TrackerAction>;
} | undefined>(undefined);

export const TrackerProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [state, dispatch] = useReducer(trackerReducer, initialState);
  return <TrackerContext.Provider value={{ state, dispatch }}>{children}</TrackerContext.Provider>;
};

export const useTracker = (): { state: TrackerState; dispatch: React.Dispatch<TrackerAction> } => {
  const context = useContext(TrackerContext);
  if (!context) {
    throw new Error("useTracker must be used within a TrackerProvider");
  }
  return context;
};