import React from "react";

interface MonthSelectorProps {
  currentMonth: string;
  onSelect: (month: string) => void;
}

const MonthSelector = ({ currentMonth, onSelect }: MonthSelectorProps) => {
  const months = Array.from({ length: 12 }, (_, i) => {
    const month = (i + 1).toString().padStart(2, "0");
    const year = "2025"; // Hardcoded for 2025; adjust as needed
    return `${year}-${month}`;
  });

  return (
    <select
      value={currentMonth}
      onChange={(e) => onSelect(e.target.value)}
      className="w-full sm:w-auto bg-lavender text-gray-800 p-2 sm:p-3 rounded-full text-sm sm:text-base shadow-soft touch-target push-button"
    >
      {months.map((month) => (
        <option key={month} value={month}>
          {new Date(month).toLocaleString("default", { month: "long", year: "numeric" })}
        </option>
      ))}
    </select>
  );
};

export default MonthSelector;